import React,{ useState } from 'react';
import { Link } from 'react-router-dom';
import './Library-icon.css';

function BackButton(props) {
  return(
    <>
      <div className="back-button">
        <Link to="/Library">戻る</Link>
      </div>
    </>
  )
}

function LibraryIconItem(props) {
  const [clicked, setClicked] = useState('');

  const iconItems = props.url.map((item, index) => 
    <>
      <li key={item.toString()} className={clicked === index ? "iconItem active" : "iconItem"}>
        <img src={`${process.env.PUBLIC_URL}/img/${props.folder}/${item}`} onClick={clicked === '' ? () => setClicked(index) : () => setClicked('')} />
      </li>
    </>
  )

  return(
    <>
      <ul className="iconItems">
        {iconItems}
      </ul>
      <div className={clicked === '' ? "mask" : "mask active"}></div>
    </>
  )
}

function LibraryIcon(props) {
  return(
    <>
      <div className="library-icons">
        <LibraryIconItem
          url = {props.url}
          folder = {props.folder}
        />
        <BackButton />
      </div>
    </>
  )
}

export default LibraryIcon;