import React, { useState,useEffect } from 'react';
import './TopPage.css';
import { Link } from 'react-router-dom';
import Swiper from 'react-id-swiper';
import Header from './Header';
import { EffectCube } from 'swiper';

function TopImage () {
  return(
    <>
      <div className="top-image top">
        <img src={`${process.env.PUBLIC_URL}/img/illust-toppage.png`} alt="" />
        <h1>大分市を中心に活動する小さなデザイン事務所です</h1>
      </div>
    </>
  )
}

function TopLinks (props) {
  const [mouseOverd, setMouseOverd] = useState(0);
  return(
    <>
      <div className="top-links">
        <Link to="Aboutus" className={mouseOverd === 1 ? "top-link active" : "top-link"} onMouseOver={() => setMouseOverd(1)} onMouseLeave={() => setMouseOverd(0)} onClick={props.setPageNumber1}>
          <img src={`${process.env.PUBLIC_URL}/img/icon-aboutus.png`} alt="" />
          <h1>About us</h1>
        </Link>
        <Link to="Library" className={mouseOverd === 2 ? "top-link active" : "top-link"} onMouseOver={() => setMouseOverd(2)} onMouseLeave={() => setMouseOverd(0)} onClick={props.setPageNumber2}>
          <img src={`${process.env.PUBLIC_URL}/img/icon-library.png`} alt="" />
          <h1>Library</h1>
        </Link>
        <Link to="Contact" className={mouseOverd === 3 ? "top-link active" : "top-link"} onMouseOver={() => setMouseOverd(3)} onMouseLeave={() => setMouseOverd(0)} onClick={props.setPageNumber3}>
          <img src={`${process.env.PUBLIC_URL}/img/icon-contact.png`} alt="" />
          <h1>Contact</h1>
        </Link>
        <Link to="Works" className={mouseOverd === 4 ? "top-link active" : "top-link"} onMouseOver={() => setMouseOverd(4)} onMouseLeave={() => setMouseOverd(0)} onClick={props.setPageNumber4}>
          <img src={`${process.env.PUBLIC_URL}/img/icon-works.png`} alt="" />
          <h1>Works</h1>
        </Link>
      </div>
    </>
  )
}

function TopLatests () {
  const images = [
    "latests-001.jpg",
    "latests-002.jpg",
    "latests-003.jpg"
  ];

  const imageItems = images.map((item, index) => 
    <div key={item.id}>
      <img src={`${process.env.PUBLIC_URL}/img/${item}`} />
    </div>
  )

  const params = {
    effect: 'cube',
    slidesPerView: 1,
    spaceBetween: 30,
    loop: true,
    pagination: {
        el: ".swiper-pagination",
        clickable: true,
    },
    navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
    },
  };

  return(
    <>
      <div className="top-latest">
        <h1>Latest - 最近の作品 -</h1>
        <Swiper {...params}>
          {imageItems}
        </Swiper>
      </div>
    </>
  )
}

function TopTweet() {
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    if (!isLoaded) {
      const s = document.createElement("script");
      s.setAttribute("src", "https://platform.twitter.com/widgets.js");
      document.body.appendChild(s);
      setIsLoaded(true);
    }
  }, []);

  return (
    <div className="tweet">
      <a className="twitter-timeline" data-width="80%" data-height="700" href="https://twitter.com/PicsDesign0726?ref_src=twsrc%5Etfw">Tweets by PicsDesign0726</a>
    </div>
  );
}

function TopPage(props) {
  return(
    <>
      <TopImage />
      <TopLinks
        setPageNumber1 = {props.setPageNumber1}
        setPageNumber2 = {props.setPageNumber2}
        setPageNumber3 = {props.setPageNumber3}
        setPageNumber4 = {props.setPageNumber4}
      />
      <TopLatests />
      <TopTweet />
    </>
  )
}

export default TopPage;