import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import './Router.css';
import Header from './Header';
import TopPage from './TopPage';
import Aboutus from './Aboutus';
import Contact from './Contact';
import Library from './Library';
import Works from './Works';

function Router(props) {
  return(
    <>
    <BrowserRouter>
      <div>
        <Header
          pageNumber = {props.pageNumber}
          setPageNumber0 = {props.setPageNumber0}
          setPageNumber1 = {props.setPageNumber1}
          setPageNumber2 = {props.setPageNumber2}
          setPageNumber3 = {props.setPageNumber3}
          setPageNumber4 = {props.setPageNumber4}
        />
      </div>
      <div className="mainContents">
        <Switch>
          <Route path="/" exact>
            <TopPage
              setPageNumber1 = {props.setPageNumber1}
              setPageNumber2 = {props.setPageNumber2}
              setPageNumber3 = {props.setPageNumber3}
              setPageNumber4 = {props.setPageNumber4}
            />
          </Route>
          <Route path="/Aboutus">
            <Aboutus />
          </Route>
          <Route exact path="/Library">
            <Library
              pageNumber = {props.pageNumber}
              setPageNumber2 = {props.setPageNumber2}
            />
          </Route>
          <Route path="/Contact">
            <Contact />
          </Route>
          <Route path="/Works">
            <Works
              setPageNumber0 = {props.setPageNumber0}
            />
          </Route>
        </Switch>
      </div>
    </BrowserRouter>
  </>
  )
}

export default Router;