import React,{ useState } from 'react';
import Header from './Header';
import './Aboutus.css';

function AboutusTopImage () {
  return(
    <>
      <div className="top-image aboutus">
        <img src={`${process.env.PUBLIC_URL}/img/illust-aboutus.png`} />
      </div>
    </>
  )
}

function AboutusText () {
  return(
    <>
      <div className="aboutus-text">
        <h1>大分市を拠点に活動する小さなデザイン事務所です</h1>
        <p>私たちは、令和二年八月に活動を始めた若いデザイナー集団です。</p>
        <p>メンバーがそれぞれの個性や得意分野を活かし、相乗効果を生み出し新たなものづくりができたらと考えています。</p>
      </div>
    </>
  )
}

function AboutusImage () {
  const [clicked, setClicked] = useState('');
  const images = [
    'aboutus_images_001.jpg',
    'aboutus_images_002.jpg',
    'aboutus_images_003.jpg',
    'aboutus_images_004.jpg',
    'aboutus_images_005.jpg',
    'aboutus_images_006.jpg',
  ];

  const imageItems = images.map((image, index) =>
    <li className={clicked === index ? 'aboutus-image-item active' : 'aboutus-image-item'} key={index}>
      <img src={`${process.env.PUBLIC_URL}/img/${image}`} onClick={clicked === '' ? () => setClicked(index) : () => setClicked('')} />
    </li>
  )

  return(
    <>
      <div className="aboutus-image">
        <ul>
          {imageItems}
        </ul>
        <div className={clicked === '' ? 'mask' : 'mask active'}></div>
      </div>
    </>
  )
}

function Aboutus() {
  return(
    <>
      <AboutusTopImage />
      <AboutusText />
      <AboutusImage />
    </>
  )
}

export default Aboutus;