import React, { useState, useEffect } from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom';
import './Header.css';

function Header(props){
  const [click, setClick] = useState(false);
  const closeMenu = () => setClick(false);
  const texts = ['デザイン事務所 ピクス','わたしたちについて','作品一覧', 'お問い合わせ・ご依頼', 'これまでのお仕事'];

  useEffect(() => {
    closeMenu();
    document.getElementById('current').addEventListener('click',closeMenu);
    return() => {
      document.getElementById('current').removeEventListener('click',closeMenu);
    }
  },[props.pageNumber])

  return(
    <>
      <div className={`header page${props.pageNumber}`}>
        <img src={`${process.env.PUBLIC_URL}/img/icon-img.png`} alt="" />
        <h1>{texts[props.pageNumber]}</h1>
      </div>
      <div className="menu-icon">
        <i className={click ? 'fas fa-times' : 'fas fa-bars'} onClick={() => setClick(!click)} />
      </div>
      <div className={click ? 'linkButton active' : 'linkButton'}>
        <Link to='/' className={props.pageNumber == 0 ? `links active page${props.pageNumber}` : `links page${props.pageNumber}`} id={props.pageNumber === 0 ? 'current' : ''} onClick={props.setPageNumber0}>
          top
        </Link>
        <Link to='/Aboutus' className={props.pageNumber == 1 ? `links active page${props.pageNumber}` : `links page${props.pageNumber}`} id={props.pageNumber === 1 ? 'current' : ''} onClick={props.setPageNumber1}>
          About us
        </Link>
        <Link to='/Library' className={props.pageNumber == 2 ? `links active page${props.pageNumber}` : `links page${props.pageNumber}`} id={props.pageNumber === 2 ? 'current' : ''} onClick={props.setPageNumber2}>
          Library
        </Link>
        <Link to='/Contact' className={props.pageNumber == 3 ? `links active page${props.pageNumber}` : `links page${props.pageNumber}`} id={props.pageNumber === 3 ? 'current' : ''} onClick={props.setPageNumber3}>
          Contact
        </Link>
        <Link to='/Works' className={props.pageNumber == 4 ? `links active page${props.pageNumber}` : `links page${props.pageNumber}`} id={props.pageNumber === 4 ? 'current' : ''} onClick={props.setPageNumber4}>
          Works
        </Link>
      </div>
    </>
  )
}

export default Header;